<template>
    <div class="main">
        <div class="header">
            <el-row>
                <el-col :span="24">
                    <el-select v-model="class_counselor" placeholder="" clearable >
                        <el-option
                        v-for="item in saleslist"
                        :key="item.name"
                        :label="item.ownerName"
                        :value="item.name">
                        </el-option>
                    </el-select>
                    <el-select v-model="sale_channel" placeholder="" clearable>
                        <el-option
                        v-for="item in sourcelist"
                        :key="item"
                        :label="item"
                        :value="item">
                        </el-option>
                    </el-select>
                    <el-date-picker
                        v-model="time"
                        @change="dateSel"
                        type="datetimerange"
                        :picker-options="pickerOptions"
                        range-separator=""
                        value-format="yyyy-MM-dd HH:mm:ss"
                        start-placeholder=""
                        align="right"
                        end-placeholder="" clearable>
                    </el-date-picker>
                    <el-button type="primary" icon="el-icon-search" style="margin-left: 10px;" @click="search"> </el-button>
                </el-col>
            </el-row>
        </div>

        <div class="content">
            <!--  -->
            <el-table
                :data="tableData"
                border
                :summary-method="getSummaries"
                show-summary
                tooltip-effect="dark"
                style="width: 100%;">
                <el-table-column
                label=""
                align="center"
                width="auto">
                    <template slot-scope="scope">
                        <a @click="showOwnerdetail(scope.row.owner)" style="cursor:default;">{{scope.row.owner}}</a>
                    </template>
                </el-table-column>
                <el-table-column
                align="center"
                width="auto"
                label="">
                    <template slot-scope="scope">
                        <a>{{scope.row.source == 'undefined'?'':scope.row.source}}</a>
                    </template>
                </el-table-column>
                <el-table-column
                prop="assigned"
                align="center"
                width="auto"
                label="">
                    <template slot-scope="scope">
                        <a @click="showListdetail('',scope.$index)" style="cursor:default">{{scope.row.assigned}}</a>
                    </template>
                </el-table-column>
                <el-table-column
                prop="called"
                align="center"
                width="auto"
                label="">
                    <template slot-scope="scope">
                        <a>{{scope.row.called}}</a>
                    </template>
                </el-table-column>
                <el-table-column
                prop="scheduled"
                align="center"
                width="auto"
                label="">
                    <template slot-scope="scope">
                        <a @click="showListdetail('',scope.$index)" style="cursor:default">{{scope.row.scheduled}}</a>
                    </template>
                </el-table-column>
                <el-table-column
                prop="trialed"
                align="center"
                width="auto"
                label="">
                    <template slot-scope="scope">
                        <a @click="showListdetail('',scope.$index)" style="cursor:default">{{scope.row.trialed}}</a>
                    </template>
                </el-table-column>
                <el-table-column
                prop="paid"
                align="center"
                width="auto"
                label="">
                    <template slot-scope="scope">
                        <a @click="showListdetail('',scope.$index)" style="cursor:default">{{scope.row.paid}}</a>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <!--  -->
        <el-dialog :title="title" :visible.sync="dialogTableVisible" class="dialog">
            <el-table :data="gridData" height="400">
                <el-table-column  :label="name" width="auto" align="center">
                    <template slot-scope="scope">
                        <span>{{scope.row}}</span>
                    </template>
                </el-table-column>
            </el-table>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="dialogOwnershow" class="dialog">
            <el-table :data="ownerData" height="400">
                <el-table-column  label="" width="auto" align="center">
                    <template slot-scope="scope">
                        <span>{{scope.row.owner}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="" width="auto" align="center">
                    <template slot-scope="scope">
                        <span>{{scope.row.source == 'undefined'?'':scope.row.source}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="" width="auto" align="center">
                    <template slot-scope="scope">
                        <span>{{scope.row.assigned}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="" width="auto" align="center">
                    <template slot-scope="scope">
                        <span>{{scope.row.called}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="" width="auto" align="center">
                    <template slot-scope="scope">
                        <span>{{scope.row.scheduled}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="" width="auto" align="center">
                    <template slot-scope="scope">
                        <span>{{scope.row.trialed}}</span>
                    </template>
                </el-table-column>
                <el-table-column  label="" width="auto" align="center">
                    <template slot-scope="scope">
                        <span>{{scope.row.paid}}</span>
                    </template>
                </el-table-column>
            </el-table>
        </el-dialog>

        <!--  -->
        <!-- <div class="block">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div> -->
    </div>
</template>

<script>
import axios from 'axios'
import { parseTime } from '@/utils/time.js'
import { constants } from 'crypto';
export default {
    data() {
        return {
            tableData1: [], // 
            gridData: [],// list
            tableData: [], // 
            ownerData: [], // list
            sale_channel: '', // 
            class_counselor: '', // 
            time: [parseTime(new Date(new Date(new Date().toLocaleDateString()).getTime()-24*60*60*1000),'{y}-{m}-{d} {h}:{m}:{s}'),parseTime(new Date(new Date(new Date().toLocaleDateString()).getTime()),'{y}-{m}-{d} {h}:{m}:{s}')], // 
            saleslist: [], // 
            sourcelist: [], // 
            salesinfo: [], // 
            starDate: '', // 
            endDate: '', // 
            dialogTableVisible: false, // 
            dialogOwnershow: false, // 
            title: '',
            class_counselor1:'',
            name:'',
            // loading: true,
            // total: 0, // 
            // currentPage: 1, // 
            // pageSize: 10, // 
            assignedlist:[], // 
            paidlist: [], // 
            scheduledlist:[], //
            trialedlist:[], // 
            pickerOptions: {
                shortcuts: [
                    {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
                            picker.$emit('pick', [start, end]);
                        }
                    }, 
                    {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, 
                    {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }
                ]
            },
        }
    },
    methods:{
        // 
        getSummaries(param) {
            const { columns, data } = param;
            const sums = [];
            columns.forEach((column, index) => {
                if (index === 0) {
                    sums[index] = '';
                    return;
                }
                const values = data.map(item => Number(item[column.property]));
                if (!values.every(value => isNaN(value))) {
                    sums[index] = values.reduce((prev, curr) => {
                    const value = Number(curr);
                    if (!isNaN(value)) {
                        return prev + curr;
                    } else {
                        return prev;
                    }
                    }, 0);
                    sums[index]
                } else {
                    sums[index] = '';
                }
            })
            return sums;
        },
        // 
        showOwnerdetail(name){
            console.log(name);
            this.dialogOwnershow = !this.dialogOwnershow;
            
            let fileterName = this.saleslist.find((data)=>{
                return data.ownerName == name
            })
            console.log(fileterName);
            if(fileterName){
                this.class_counselor1 = fileterName.name;
            }

            let data = {
                starttime: this.starDate, // 
                endtime: this.endDate, // 
                owner: this.class_counselor1, // 
                source: this.sale_channel // 
            }
            axios.post('https://mars1.geek-8.com/trackleads', data).then((res)=>{
                if(res.status == 200){
                    const datas = res.data;
                    this.tableData1 = JSON.parse(decodeURIComponent(datas.success));
                    // this.ownerData = [];
                    this.ownerData = this.tableData1;
                    for(var i in this.ownerData){
                        let fileterOwner = this.saleslist.find((data)=>{
                            return data.name == this.ownerData[i].owner
                        })
                        if(fileterOwner){
                            this.ownerData[i].owner = fileterOwner.ownerName;
                        }
                    }
                }
            }).catch((error)=>{
                console.log(error);
            })
        },
        // 
        showListdetail(name,index){
            this.dialogTableVisible = !this.dialogTableVisible;
            this.gridData = [];
            switch(name){
                case '':
                    this.title = '';
                    this.name = '';
                    this.assignedlist = this.tableData[index].assignedlist;
                    this.gridData = this.assignedlist;
                    break;
                case '':
                    this.title = '';
                    this.name = '';
                    this.scheduledlist = this.tableData[index].scheduledlist;
                    this.gridData = this.scheduledlist;
                    break;
                case '':
                    this.title = '';
                    this.name = ''
                    this.trialedlist = this.tableData[index].trialedlist;
                    this.gridData = this.trialedlist;
                    break;
                case '':
                    this.title = '';
                    this.name = ''
                    this.paidlist = this.tableData[index].paidlist;
                    this.gridData = this.paidlist;
                    break;
                    default:
                    break;
            }
        },
        // 
        dateSel(val){
            // console.log(val);
            if(val == null){
                this.starDate = '';
                this.endDate = '';
            }else{
                this.starDate = val[0]; // 
                this.endDate = val[1]; // 
                this.getData();
            }
        },
        // 
        async getData(){
            const data = {
                starttime: this.starDate, // 
                endtime: this.endDate // 
            }
            await axios.post('https://mars1.geek-8.com/tllist',data).then((res)=>{
                if(res.status == 200){
                    const temp = res.data;
                    this.salesinfo = JSON.parse(decodeURIComponent(temp.salesinfo));
                    for(var i in this.salesinfo){
                        this.saleslist.push({name:i,ownerName:this.salesinfo[i].ownerName}); // 
                    }
                    this.sourcelist = JSON.parse(decodeURIComponent(temp.sourcelist)); // 
                }
            }).catch((error)=>{
                console.log(error);
            })
        },
        // 
        search(){
            let data = {
                starttime: this.starDate, // 
                endtime: this.endDate, // 
                owner: this.class_counselor, // 
                source: this.sale_channel // 
            }
            axios.post('https://mars1.geek-8.com/trackleads', data).then((res)=>{
                if(res.status == 200){
                    const datas = res.data;
                    this.tableData = JSON.parse(decodeURIComponent(datas.success));
                    for(var i in this.tableData){
                        let fileterSales = this.saleslist.find((data)=>{
                            return data.name == this.tableData[i].owner
                        });
                        if(fileterSales) {
                            this.tableData[i].owner = fileterSales.ownerName
                        }
                    }
                }
            }).catch((error)=>{
                console.log(error);
            })
        }
    }
}
</script>

<style scoped lang="scss">
.main{
    width: 100%;
    height: 100%;
    margin: 0 auto;
    .header{
        width: 100%;
        height: 45px;
        margin-top: 20px;
        .el-row{
            .el-col{
                .el-select{
                    margin-right: 15px;
                }
            }
        }
    }
    .content{
        margin-top: 20px;
        margin-bottom: 30px;
    }
    .block{
        text-align: center;
    }
    .dialog{
        ul{
            // width: 100%;
            height: 250px;
            overflow-y: scroll;
            text-align: left;
            li{
                list-style: none;
                line-height: 30px;
                font-size: 17px;
                color: #000;
            }
        }
    }
}
</style>